import React, { useState, useEffect, useMemo } from 'react';
import './App.css';
import PlayerForm from './PlayerForm';
import FormationManager from './FormationManager';
import PlayerList from './PlayerList';
import Modal from './Modal'; // Modal  import

// ---    () ---
// localStorage  ,     .
